import styled from "styled-components";

export default function JettImage ({ src, className }) {
    return(
        <Wrapper className={`jett-img-wrapper ${className}`}>
            <Content src={src} alt="jett marketing assets" draggable="false" />
        </Wrapper>
    )
}

const Wrapper = styled.div`
    max-width: 100%;
    height: auto;
`

const Content = styled.img`
    max-width: 100%;
    height: auto;
`