import { Modal, Card as NRCard } from "@nextui-org/react";
import { Heading, Row, Button, Column } from "./layout";
import { MdOutlinePayment } from 'react-icons/md';
import { BsCoin } from 'react-icons/bs';
import { FaEthereum } from 'react-icons/fa';
import { Text } from "./layout";

export const PlanModal = ({ visible, onCloseRequest, planType, onCrypto }) => {
    return (
        <Modal
            closeButton
            blur
            width='600px'
            aria-labelledby="modal-title"
            open={visible}
            onClose={onCloseRequest}
        >
            <Modal.Header>
                <Heading size="32px" id="modal-title">
                    Subscription Plan
                </Heading>
            </Modal.Header>
            <Modal.Body>
                <NRCard isHoverable className='nr-card' variant="bordered">
                    <a href={planType ? "https://checkout.stripe.com/pay/cs_live_a1GZZtuUajVN1gQJEWzZKn2JzjfQC8GruvOLdQQrkjGxJoFw8vVWUre2IH#fidkdWxOYHwnPyd1blppbHNgWjA0SUszc3VJV1BpYF1Vbl1pbmZtXVBcbGh0ZH1EQW9QYG40VU10Y3JnTVZgaFRmNmJ%2FX39AVlBgf3JDbTA3dVw0MFYzSkFpcGB3NGpISkRPPEtLT2dVfXVdNTVoZ1JLb39cdCcpJ3VpbGtuQH11anZgYUxhJz8nMWJyPX9jN243Y1F8MEhMZEBAJ3gl" : "https://checkout.stripe.com/pay/cs_live_a1EujznIwbP3vlC6ARY5SzopSfE1Z2Zehk78T5GBc1i8Y4eQisamAAcXZR#fidkdWxOYHwnPyd1blppbHNgWjA0SUszc3VJV1BpYF1Vbl1pbmZtXVBcbGh0ZH1EQW9QYG40VU10Y3JnTVZgaFRmNmJ%2FX39AVlBgf3JDbTA3dVw0MFYzSkFpcGB3NGpISkRPPEtLT2dVfXVdNTVoZ1JLb39cdCcpJ3VpbGtuQH11anZgYUxhJz8nM2pAZlVzNWdQMms3YndoNTU0J3gl"} style={{ color: "black", textDecoration: "none" }} rel="noreferrer" target="_blank">
                        <NRCard.Body>
                            <Row gap="10px">
                                <MdOutlinePayment color="#FF6600" size="24px" /> Subscription plan through typical method
                            </Row>
                        </NRCard.Body>
                    </a>
                </NRCard>
                <NRCard isHoverable className='nr-card' variant="bordered">
                    <NRCard.Body onClick={onCrypto}>
                        <Row gap="10px">
                            <BsCoin color="#FF6600" size="22px" /> Subscription plan through crypto method
                        </Row>
                    </NRCard.Body>
                </NRCard>
            </Modal.Body>
            <Modal.Footer>
                <Row justifyContent="center" style={{ flex: "1" }}>
                    <Button color='white' bg='#FF6600' onClick={onCloseRequest}>
                        Close
                    </Button>
                </Row>
            </Modal.Footer>
        </Modal>
    )
}

export const CryptoModal = ({ visible, onCloseRequest, planType }) => {
    return (
        <Modal
            closeButton
            blur
            width='600px'
            aria-labelledby="modal-title"
            open={visible}
            onClose={onCloseRequest}
        >
            <Modal.Header>
                <Heading size="32px" id="modal-title">
                    Crypto Payment
                </Heading>
            </Modal.Header>
            <Modal.Body>
                <Column alignItems="center">
                <Text textAlign="center" style={{paddingBottom:"10px"}}>
                    Please make payment to below contract address and sure that you make payment exactly.
                    After made payment, please contact to team members and receive code.
                </Text>
                <Heading>
                    {
                        planType ? <Row gap="5px">0.5<FaEthereum/></Row> : <Row>0.2<FaEthereum gap="5px" /></Row>
                    }
                </Heading>
                <a style={{color:"black", fontFamily:"Montserrat"}} href="https://rinkeby.etherscan.io/address/0x06b8e43614e4ddd922ec9329d02be861b7f4fdcf#writeContract" target="_blank" rel="noreferrer">
                    0x06B8e43614e4Ddd922eC9329d02bE861B7F4FDcF
                </a>
                </Column>
            </Modal.Body>
            <Modal.Footer>
                <Row justifyContent="center" style={{ flex: "1" }}>
                    <Button color='white' bg='#FF6600' onClick={onCloseRequest}>
                        Close
                    </Button>
                </Row>
            </Modal.Footer>
        </Modal>
    )
}